/* ----- Set the base font size on the HTML element for easier use of rem, em, and px. ----- */
html {
  font-size: 62.5%; /* 1rem = 10px (so, 1.6rem = 16px) */
  box-sizing: border-box;
  width: 100%;
  scroll-behavior: smooth;
  scroll-padding-top: 0rem;
}

/* ----- Inherit box-sizing in all elements and pseudo-elements ----- */
*,
*::before,
*::after {
  box-sizing: inherit;
}

/* ----- Set margins and padding in the body to avoid unwanted spaces ----- */
body {
  font-optical-sizing: auto;
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-size: 1.8rem;
  color: #000000;
  margin: 0;
  padding: 0;
  height: 100%;
  line-height: 1.8;
}

/** ----- Global styles ----- **/
p {
  font-size: 2rem;
  color: #000000;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  margin: 5rem;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

img {
  height: auto;
  display: block;
}

/** Media Queries **/
.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 5rem;
}

.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.none-display {
  display: none;
}

.button {
  background: none;
  border: none;
}

.nav {
  position: absolute;
  height: 50%;
  width: 100%;
  background-color: #131313;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateY(-100%);
  transition: 0.5s;
  z-index: 1;
}
@media (min-width: 768px) {
  .nav {
    transform: translateY(0);
    position: static;
    margin: 2.5rem auto;
    width: 60%;
    padding: 1rem 5rem;
    border-radius: 5rem;
  }
}
@media (min-width: 1200px) {
  .nav {
    width: 40%;
  }
}
.nav ul {
  display: inherit;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav ul {
    flex-direction: row;
    gap: 4rem;
  }
}
.nav ul li {
  display: inherit;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
.nav ul li:last-child {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .nav ul li {
    margin-top: 0rem;
    gap: 1rem;
  }
  .nav ul li:last-child {
    margin-top: 0rem;
  }
}
.nav ul li a {
  color: #b292ff;
  font-size: 2.5rem;
  transition: 0.2s;
}
.nav ul li a:hover {
  color: #ffffff;
  transform: scale(1.2);
}
.nav ul li .nav__img__link {
  width: 3.5rem;
  height: 3.5rem;
}
.nav ul li .nav__img__link:hover {
  transform: translateY(-0.5rem);
  transform: scale(1.2);
  filter: brightness(0) invert(1);
}

.nav__button {
  position: absolute;
  top: 5rem;
  right: 5rem;
  width: 4rem;
  filter: brightness(100) invert(0);
  transition: 0.2s;
  transform: scale(1.3);
  z-index: 1;
}
.nav__button:hover, .nav__button:focus {
  cursor: pointer;
  filter: none;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}

.active {
  transform: translateY(0);
}

body {
  background-color: #0c0c0d;
}

.hero {
  display: flex;
  flex-direction: column-reverse;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .hero {
    flex-direction: row;
    margin-top: 15rem;
  }
}
.hero .hero__info h1 {
  margin: 0;
  font-weight: 400;
  font-size: 5rem;
  text-align: center;
}
@media (min-width: 1200px) {
  .hero .hero__info h1 {
    text-align: left;
  }
}
.hero .hero__info p {
  color: #6f6f6f;
  font-weight: 300;
  text-align: center;
}
@media (min-width: 1200px) {
  .hero .hero__info p {
    text-align: left;
  }
}
.hero .hero__info p span {
  font-weight: 400;
  color: #ffffff;
}
.hero .hero__buttons__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1200px) {
  .hero .hero__buttons__container {
    flex-direction: row;
    gap: 5rem;
    margin-top: 5rem;
  }
}
.hero .hero__buttons__container .hero__button {
  display: inherit;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
  height: 5rem;
  padding: 1rem 4rem;
  border-radius: 5rem;
  transition: 0.2s;
}
@media (min-width: 768px) {
  .hero .hero__buttons__container .hero__button {
    width: 35rem;
    margin: auto;
  }
}
@media (min-width: 1200px) {
  .hero .hero__buttons__container .hero__button {
    padding: 0.5rem 4rem;
  }
}
.hero .hero__buttons__container .hero__button--linkedin {
  display: inherit;
  gap: 1rem;
  color: #b292ff;
  background: linear-gradient(to bottom, #232323, #434343, #232323);
}
.hero .hero__buttons__container .hero__button--linkedin img {
  width: 3.5rem;
}
.hero .hero__buttons__container .hero__button:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.hero picture {
  display: flex;
  justify-content: end;
}
@media (min-width: 768px) {
  .hero picture {
    width: 50rem;
    margin: auto;
  }
}
.hero picture img {
  filter: drop-shadow(0px 0px 100px rgba(255, 255, 255, 0.5));
}
@media (min-width: 1200px) {
  .hero picture img {
    width: 35rem;
  }
}
@media (min-width: 1200px) {
  .hero section,
  .hero picture {
    width: 50%;
  }
}